import React from 'react'
import {Link} from 'react-router-dom'

const Item = (props) => {
    return (
        <div className="footer-list">
            <h6>{props.category}</h6>
            <div className="links">
                {props.links.map((l) => {
                    return (
                    <Link key={l.to} to={l.to} className="footer-item">
                        {l.link}
                    </Link>
                    );
                })}
            </div>
        </div>
    )
}

export default Item
