import React, {useState} from 'react'
import {Link} from 'react-router-dom'
import {FeaturesData, PlansData, BusinessData} from '../data';

import {FaBusinessTime, FaBloggerB} from 'react-icons/fa'
import {BsLayoutTextSidebar} from 'react-icons/bs'
import {CgClose} from 'react-icons/cg'
import {RiPriceTagFill} from 'react-icons/ri'
import {MdApps,MdKeyboardArrowDown} from 'react-icons/md'
import {AiOutlineUnorderedList, AiOutlineQuestionCircle} from 'react-icons/ai'


const Sidebar = (props) => {

    const List = (props) => {
        return (
            <div className='dropdown-links-items'>
                <Link to={props.item.to} className='sidebar-dropdown-link'>
                    <span>{props.item.icon}</span>
                    {props.item.title}
                </Link>
            </div>
        )
    }

    const [list,showList] = useState(false);
    function show(){
        showList(!list);
    }

    const [list2,showList2] = useState(false);
    function show2(){
        showList2(!list2);
    }

    const [list3,showList3] = useState(false);
    function show3(){
        showList3(!list3);
    }

    return (
        <div className='mobile-sidebar'>
             <div className='sidebar-logo-row'>
                <Link to='/'>
                    <div className='mobile-logo-container'></div>
                    <h6>HostPassword</h6>
                </Link>
                <button onClick={props.click}><CgClose/> </button>
            </div>

            <div className='mobile-sidebar-links'>

                <div className='sidebar-dropdown'>
                    <div className='nav-sidebar-link' onClick={show}>
                        <Link to='/' className='nav-anchor'>
                            <AiOutlineUnorderedList />
                            Features 
                        </Link>
                        <div className='dropdown-arrow'>
                            <MdKeyboardArrowDown/> 
                        </div>
                    </div>
                    {list===true && FeaturesData.map(item=>
                                    <List key={item.title} item={item} />) }
                </div>

                <div className='nav-sidebar-link'>
                    <Link to='/HowItWorks' className='nav-anchor'><AiOutlineQuestionCircle/> How it works </Link>
                </div>
                <div className='sidebar-dropdown'>
                    <div className='nav-sidebar-link' onClick={show2}>
                        <Link to='/' className='nav-anchor'>
                            <BsLayoutTextSidebar />
                            Plans
                        </Link>
                        <div className='dropdown-arrow'>
                            <MdKeyboardArrowDown/> 
                        </div>
                    </div>
                    {list2===true && PlansData.map(item=>
                                    <List key={item.title} item={item} />) } 
                </div>
                              
                <div className='sidebar-dropdown'>
                    <div className='nav-sidebar-link' onClick={show3}>
                        <Link to='/' className='nav-anchor'>
                            <FaBusinessTime />
                            Business 
                        </Link>
                        <div className='dropdown-arrow'>
                            <MdKeyboardArrowDown/> 
                        </div>
                    </div>
                    {list3===true && BusinessData.map(item=>
                                    <List key={item.title} item={item} />) }  
                </div>

                <div className='nav-sidebar-link'>
                    <Link to='/Pricing' className='nav-anchor'><RiPriceTagFill/> Pricing</Link>
                </div>
                <div className='nav-sidebar-link'>
                    <Link to='/Apps' className='nav-anchor'><MdApps/> Apps</Link>
                </div>
                <div className='nav-sidebar-link'>
                    <Link to='/Blog' className='nav-anchor'><FaBloggerB/> Blog</Link>
                </div>
            </div>

            <div className='sidebar-auth'>
                <div className='nav-auth-link nav-login'>
                    <Link to='/Login'>Log In</Link>
                </div>
                <div className='nav-auth-link nav-signup'>
                    <Link to='/Signup'>Sign Up</Link>
                </div>
            </div>

            <div className='space'></div>
        </div>
    )
}

export default Sidebar
