import React from 'react'
import {Link} from 'react-router-dom'
import {Business} from '../data'

const BusinessMenu = () => {
    return (
        <div className='megamenu'>
        {Business.map((props) => {
            return(
                <div className={`inner-megamenu ${props.class}`}>
                    <h5>{props.category}</h5>
                    {props.links.map((l) => {
                        return(
                            <Link to={l.to}  className='megamenu-link'>
                                <div className='icon'>
                                    {l.icon}
                                </div>
                                <div className='block'>
                                    <h6>{l.title}</h6>
                                    <p>{l.description}</p>
                                </div>
                            </Link>
                        )
                    })}
                    <div className='last-link'>
                        <Link to={props.to1}>
                            {props.lastIcon}
                            {props.link1}
                        </Link>
                    </div>
                </div>
            )
        })}
    </div>
    )
}

export default BusinessMenu
