import React, {useState} from 'react';
import {Link} from 'react-router-dom';

import { FormattedMessage } from "react-intl";
import SelectLanguage from '../SelectLanguage/SelectLanguage';

import {FooterData, SocialMedia} from './data';
import './footer.scss';
import {GrApple} from 'react-icons/gr'
import {FaGooglePlay} from 'react-icons/fa';
import Item from './Item'

const DownloadData = [
    {
        icon: <FaGooglePlay />,
        text:'GET IT ON',
        title:'Google Play'
    },
    {
        icon: <GrApple />,
        text:'Download on the',
        title:'App Store'
    }
]


const Footer = (props) => {

    const FooterItems = FooterData.map((l) => (
        <Item key={l.category} {...l} />
    ))

    var first=FooterItems.slice(0,2)
    var second=FooterItems.slice(2,5)

    const [email, setEmail] = useState('');

    return (
        <footer>
            <div className='row1'>
                <div className='col1'>
                    <div className='logo-row'>
                        <Link to='/'>
                            <div className='logo-container'></div>
                            <h5>HostPassword</h5>
                        </Link>
                    </div>
                    <p className='p'>
                        <FormattedMessage id="footer-paragraph" />
                    </p>
                    <div className='email-container'>
                        <span>
                            <FormattedMessage id="footer-paragraph2" defaultMessage='abc' />
                        </span>
                        <div className='email'>
                            <input
                                type='email'
                                name='email'
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                placeholder='Enter your email'
                                className='email-input'
                            />
                            <button type='submit'> <FormattedMessage id="footer-button" /></button>
                        </div>
                    </div>

                    <div className='buttons'>
                        <span className='span'>HostPassword App</span>
                        <div className='inner-buttons'>
                            {DownloadData.map((props) => {
                                return(
                                    <button type='button'>
                                        <span>{props.icon}</span>
                                        <div className='icons'>
                                            <p>{props.text}</p>
                                            <h6>{props.title}</h6>
                                        </div>
                                    </button>
                                )
                            })}
                        </div>
                    </div>
                </div>

               <div className='col2'>
                    <div className='links-col1'>
                        {first}
                    </div>
                    <div className='links-col2'>
                        {second}
                    </div>
               </div>
            </div>

            <div className='social-media-row'>
                {SocialMedia.map((props) => {
                    return(
                        <div>
                            <Link to={props.to}>{props.icon}</Link>
                        </div>
                    )
                })}
            </div> 

         

            <div className='copyright-row'>
                <div className='copyright'>
                    <p>
                        © <FormattedMessage id="footer-paragraph3" />
                    </p>
                    <Link to='/'><FormattedMessage id="footer-link1" /></Link>
                    <div className='line'>|</div>
                    <Link to='/'><FormattedMessage id="footer-link2" /></Link>
                </div>
                <div className='select-col'>
                <SelectLanguage
                    setLanguage={props.setLanguage}
                    language={props.language}
                />
                </div>
            </div> 
        </footer>
    )
}

export default Footer
