import React, {useState, useEffect} from 'react';
import {Link} from 'react-router-dom';
import clsx from "clsx";

import {BsTriangleFill} from 'react-icons/bs'
import {MdKeyboardArrowDown} from 'react-icons/md'

import './navbar.scss'
import MobileNav from '../MobileNav/MobileNav'
import FeaturesMenu from './Megamenus/FeaturesMenu'
import PlansMenu from './Megamenus/PlansMenu'
import BusinessMenu from './Megamenus/BusinessMenu'

const Navbar = () => {  

    const [isSticky, setIsSticky] = useState(false);

    useEffect(() => {
    const nav = document.querySelector(".nav");
    const navHeight = nav.scrollHeight;

    const handleScroll = () => {
      const scrollHeight = window.pageYOffset;
      setIsSticky(scrollHeight > 180 ? true : false);
    };

    window.addEventListener("scroll", handleScroll);

    return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    return (
        <div id="navbar" className={clsx("nav", isSticky ? "nav__sticky" : "")}>
            <Link to='/' className='nav-logo'>
                <div className='logo-container'></div>
                <h6>HostPassword</h6>
            </Link>

            <div className='nav-links'>
                <div className='nav-link features-link'>
                    <Link to='/' className='inner-nav-link'>Features <MdKeyboardArrowDown className='arrow1'/></Link>
                    <div className='megamenu-arrow'><BsTriangleFill /></div>
                    <FeaturesMenu />
                </div>
                <div className='nav-link how-it-works-link'>
                    <Link to='/HowItWorks' className='inner-nav-link'>How it works</Link>
                </div>
                <div className='nav-link plans-link'>
                    <Link to='/' className='inner-nav-link'>Plans<MdKeyboardArrowDown className='arrow1'/></Link>
                    <div className='megamenu-arrow'><BsTriangleFill /></div>
                    <PlansMenu />
                </div>
                <div className='nav-link business-link'>
                    <Link to='/' className='inner-nav-link'>Business <MdKeyboardArrowDown className='arrow1'/></Link>
                    <div className='megamenu-arrow'><BsTriangleFill /></div>
                    <BusinessMenu />
                </div>
                <div className='nav-link pricing-link'>
                    <Link to='/Pricing' className='inner-nav-link'>Pricing</Link>
                </div>
                <div className='nav-link apps-link'>
                    <Link to='/Apps' className='inner-nav-link'>Apps</Link>
                </div>
                <div className='nav-link blog-link'>
                    <Link to='/Blog'  className='inner-nav-link' >Blog</Link>
                </div>
             </div>

            <div className='nav-auth'>
                <div className='nav-auth-link nav-login'>
                    <Link to='/Login'>Log In</Link>
                </div>
                <div className='nav-auth-link nav-signup'>
                    <Link to='/Signup'>Sign Up</Link>
                </div>
            </div>

            
            <MobileNav />
        </div>
    )
}

export default Navbar
